
html {
    background-color: #f7f7f7;
}
 
.app-name {
    font-size: 2.3rem;
    color: orange;
    margin-bottom: 16px;
}
 
.App {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 600px;
    min-height: 440px;
    background-color: rgb(255, 255, 255);
    text-align: center;
    margin: 128px auto;
    border-radius: 10px;
    padding-bottom: 32px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}
 
.city-search {
    width: 100%;
    max-width: 400px;
    box-sizing: border-box;
    border: 2px solid rgb(204, 204, 204);
    outline: none;
    border-radius: 20px;
    font-size: 16px;
    background-color: #e5eef0;
    background-position: 10px 12px;
    background-repeat: no-repeat;
    padding: 12px 40px 12px 40px;
    -webkit-transition: width 0.4s ease-in-out;
    transition: width 0.4s ease-in-out;
    color: #333;
}
 
.city-search:focus {
    width: 100%;
}
 
.city-name {
    font-size: 1.5rem;
    color: #444;
    margin-bottom: 8px;
}
 
.date {
    font-size: 1.25em;
    font-weight: 500;
    color: #777;
}
 
.icon-temp {
    font-size: 3rem;
    font-weight: 700;
    color: #1e2432;
    text-align: center;
}
 
.deg {
    font-size: 1.3rem;
    vertical-align: super;
}
 
.des-wind {
    font-weight: 500;
    color: #666;
}
 
.error-message {
    display: block;
    text-align: center;
    color: #d32f2f;
    font-size: 24px;
    margin-top: auto;
}
 
.Loader {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}
 
.Loader>div {
    margin: 0 auto;
}
 
.weather-icon {
    display: flex;
    justify-content: center;
    align-items: center;
}
 
.weather-icon img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}